export default {
  head(){
    return{
      title: this.work.title
    }
  },
  async asyncData({ params, store, redirect }) {
    if (store.state.works[params.work]) {
      return { work: store.state.works[params.work] };
    } else {
      redirect("/");
    }
  },
  data() {
    return {};
  },
  methods: {},
};
